/* DescriptionOfItems.css */

.container {
  height: 100vh;
  width: 100%;
    display: flex; /* Use flexbox for layout */
    flex-wrap: wrap;
    
    justify-content: center; /* Center align items */
    padding: 20px;
    text-align: center;
  }

  .specifications {
    margin: auto;
  }

  .descriptionandspec{
    height: 100%;
    width: 100%;
    background-color: #0056b3;
    display: flex;
    flex-direction:column;
  }
  
  .item-image {
    border-radius: 5px;
    max-width: 860px; /* Adjust max-width for image */
    height: auto;
    margin-right: 20px; /* Space between image and details */
  }

  .comments-section {
    width: 80%;
    
    padding: 20px;
    
    
    margin-top: 30px;
  }
  
  .comments-section h2 {
    font-size: 1.8rem;
    color: #333;
    margin-bottom: 15px;
    border-bottom: 2px solid #e63946;
    padding-bottom: 10px;
  }
  
  .comment-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 20px;
    border: none;
  }
  
  .comment-form input {
    width: 100%;
    padding: 15px;
    border: none;
    
    
    
    font-size: 1rem;
    
  }
  
  .comment-form button {
    align-self: flex-end;
    background-color: #e63946;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .comment-form button:hover {
    background-color: darkred;
  }
  
  .comments-list {
    
    margin-top: 20px;
  }
  
  .comment {
    position: relative;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    background-color: #ffffff;
    padding: 15px;
    gap:10px;
    margin-bottom: 10px;
    border: 0.1px solid #ddd;
    
  }
  
  .comment p {
    font-size: 1.1rem;
    color: #333;
    margin: 0;
  }
  
  .comment .comment-name {
    font-weight: bold;
    color: #e63946;
    margin-bottom: 5px;
  }
  
  .comment .comment-text {
    color: #555;
  }

  #viewson{
    font-size: 12px;
  }

  #nooflikes{
    position: absolute;
    bottom: 8px;
    right: 10px;
    font-size: 13px;
  }

  .comment button{
    position: absolute;
    bottom: 5px;
    right: 30px;
  }


  @media (max-width: 768px) {
    .item-image {
      border-radius: 5px;
      max-width: 80%;
      height: auto;
      margin-right: 20px; 
        
    }
  }
  
  .item-details {
    max-width: 400px; /* Limit width of details section */
    text-align: left; /* Align text to the left */
  }
  
  .item-title {
    font-size: 1.5rem; /* Adjust title font size */
    margin: 10px 0;
  }
  
  .item-price {
    font-size: 1.25rem; /* Adjust price font size */
  }
  
  .item-description {
    font-size: 1rem; /* Adjust description font size */
    margin: 10px 0;
  }
  
  .add-to-cart {
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    border-radius: 5px;
    padding: 8px 16px; /* Adjust padding */
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px; /* Adjust margin */
  }
  
  .add-to-cart:hover {
    background-color: #0056b3; /* Darker shade for hover */
  }
  
  .specifications {
    margin-top: 15px; /* Adjust margin */
  }
  
  .specifications-table {
    border: 1px solid #ccc;
    border-collapse: collapse;
    width: 100%; /* Full width */
    margin: 0 auto; /* Center table */
    text-align: left;
  }
  
  .specifications-table td {
    border: 1px solid #ddd;
    padding: 5px; /* Adjust padding */
  }
  
  .specifications-table th {
    background-color: #f9f9f9;
    font-weight: bold;
  }
  
  .specifications-table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .specifications-table tr:hover {
    background-color: #e2e2e2;
  }
  